import React, { useState } from 'react';
import { RiskMatrix } from "./component/RiskMatrix/RiskMatrix"
import './App.css';
import { IRiskItem } from './component/Interfaces/IRiskItem';
import { RiskList } from './component/RiskItem/RiskList';
import { Box, Tab, Tabs } from '@mui/material';


interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div

      hidden={value !== index}

    >
      {children}
    </div>
  );
}

function App() {

  
  const [risks, setRisks] = useState<IRiskItem[]>([]);
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  function addItem(obj:IRiskItem){
    setRisks([...risks, obj]);
    console.table(risks);
  }
  
  function change(obj:IRiskItem){
    setRisks(risks.map(val=>{
      if(obj.id === val.id) 
        return obj;
      else 
        return val;
    }));
  }

  function remove(id:number){
    setRisks(
      risks.filter(obj=>obj.id!==id)
    )
  }

  return (
    <>

    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="Матриця" />
          <Tab label="Налаштування" />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <RiskMatrix list={risks} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <RiskList add={addItem} list={risks} change={change} remove={remove}/>
      </TabPanel>

    </Box>
     
    </>
   
  );
}

export default App;
