import React, { useState } from 'react'
import { RiskLevel } from '../Classes/Classes';
import "./RiskItem.css"
import Button from '@mui/material/Button';
import AddIcon from '@mui/icons-material/Add';
import { IRiskEventHendler } from '../Interfaces/IRiskItem';

export const CreateRiskItem:React.FC<IRiskEventHendler> = (props) =>{
    
    
    let data = Object.keys(RiskLevel)
    .map(index=> {
        return {
            id: RiskLevel[index].id, 
            value: RiskLevel[index].name, 
            description: RiskLevel[index].getDescription()
        }
    });
    
    const selectOption = data.map(row=>
        <option key={row.value} value={row.value}>{row.description}</option>
        );
        
    const [criticality,setCriticality] = useState(RiskLevel.Midle.name);
    const [likelihood,setLikelihood] = useState(RiskLevel.Midle.name);
    const [description,setDescription] = useState("");
        
    function onChangeLikelihood(event:React.ChangeEvent<HTMLSelectElement>){
        setLikelihood(event.target.value);
    }
    
    function onChangeCriticality(event:React.ChangeEvent<HTMLSelectElement>){
        setCriticality(event.target.value);
    }
    

    function onChangeDescription(event:React.ChangeEvent<HTMLInputElement>){
        setDescription(event.target.value);
    }

    function addNew(event:React.MouseEvent<HTMLButtonElement>){
        props.add && props.add({criticality:RiskLevel[criticality], likelihood:RiskLevel[likelihood], description, id:Date.now()});
        setCriticality(RiskLevel.Midle.name);
        setLikelihood(RiskLevel.Midle.name);
        setDescription('');
    }
    return(
    <>
        <h2>Додавання ризику</h2>
        <div className='row'>
            <div className='coll'> 
                <label>
                    <span>Ім'я ризику</span>
                    <input value={description} className='input' type="text" id="riskName" onChange={onChangeDescription} />
                </label>
            </div>
           
            <div className='coll'>
                <label>
                    <span>Вірогідність</span>
                    <select value={likelihood} className='input' name="likelihood" onChange={onChangeLikelihood}>
                        {selectOption}
                    </select>
                </label>
            </div>
            
            <div className='coll'>
                <label>
                    <span>Критичність</span>
                    <select value={criticality} className='input' name="criticality" onChange={onChangeCriticality}>
                        {selectOption}
                    </select>
                </label>
            </div>
            

            <div className='coll'>
                <Button variant="outlined" startIcon={<AddIcon />} onClick={addNew}>Додати</Button>
            </div>
            
        </div>
    </>
    )
}