import { IListRiskItems, IRiskItem } from "../Interfaces/IRiskItem"
import "./RiskMatrix.css"
import "./RiskMatrix_theme2.css"
import { useState } from "react"

export const RiskMatrix:React.FC<IListRiskItems> = (props) =>{

    const [riskList, setRiskList] = useState<IRiskItem[]>([]);
    
    function filterCount(criticality:string, likelihood:string){
        return props.list.filter(val=> val.likelihood.name===likelihood && val.criticality.name===criticality);
    }

    function showRisk(criticality:string, likelihood:string, cellNumber:number){
        setRiskList(()=>filterCount(criticality, likelihood));
    }

    let paternColor = "very-low,very-low,low,midle,midle,midle,high,very-high,very-high".split(',');
    const coordinateAxis = ["VeryLow","Low","Midle","High","VeryHigh"];
    const filterTemp:string[][] = [];

    let grid:JSX.Element[] = [];
    let counterKey = -1;
    for(let axisY of coordinateAxis){
        console.log("render cell");
        let counter = 0;
        const row:JSX.Element[] = [];

        for(let axisX of coordinateAxis){
            counterKey++;
            let keyCell = counterKey;
            const className = `item ${paternColor[counter]}`;

            filterTemp[keyCell] = [axisX, axisY];
            const onClickHandler = function(){showRisk(axisX, axisY, keyCell)};
            
            row.push(
                <div 
                    key={keyCell} 
                    onClick={onClickHandler} 
                    className={className}>{filterCount(axisX, axisY).length || ""}
                </div>
            );

            counter += 1;
        }

        paternColor.shift();

        grid = [...row, ...grid];
    }

    return(
        <div className="wraper">
            <div  className="matrix-grid">
                <div key={counterKey++} className="likelihood">
                    <span>Вірогідність</span>
                </div>

                <>
                {grid}
                </> 
            
                <div key={counterKey++} className="criticality">
                    Критичність
                </div>
            </div>
            <div>
                <h2>Ризики</h2>
                <ol>
                    {riskList.map(data =>
                        <li key={"li_" +data.id}>{data.description}</li>
                    )}
                </ol>
            </div>
            
        </div>
        
    )
}