import { IRiskItem } from "../Interfaces/IRiskItem";
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import SaveIcon from '@mui/icons-material/Save';
import Button from '@mui/material/Button';
import "./RiskItem.css"
import { useState } from "react";
import { RiskLevel } from "../Classes/Classes";

export const RiskItem:React.FC<IRiskItem>  = (props) => {

    console.log("RiskItem");

    const [changeMode, setChangeMod] = useState(false);

    let result = null;

    const [criticality,setCriticality] = useState(props.criticality.name);
    const [likelihood,setLikelihood] = useState(props.likelihood.name);
    const [description,setDescription] = useState(props.description);
        
    function onChangeLikelihood(event:React.ChangeEvent<HTMLSelectElement>){
        setLikelihood(event.target.value);
    }
    
    function onChangeCriticality(event:React.ChangeEvent<HTMLSelectElement>){
        setCriticality(event.target.value);
    }
    

    function onChangeDescription(event:React.ChangeEvent<HTMLInputElement>){
        setDescription(event.target.value);
    }

    function save(event:React.MouseEvent<HTMLButtonElement>){
        props.change && props.change({criticality:RiskLevel[criticality], likelihood:RiskLevel[likelihood], description, id:props.id});
        setChangeMod(true);
    }

    function remove(event:React.MouseEvent<HTMLButtonElement>){
        props.remove && props.remove(props.id);
    }
    
    if(changeMode){

        let data = Object.keys(RiskLevel).map(index=> {return {
            id: RiskLevel[index].id, 
            value: RiskLevel[index].name, 
            description: RiskLevel[index].getDescription()
        }});
        const selectOption = data.map(row=>
            <option key={row.value} value={row.value}>{row.description}</option>
            );
        
        result = (
            <div className="row">
                <div className='coll'>
                    <input className='input' type="text" id="riskName" value={description} onChange={onChangeDescription}/>
                </div>
            
                <div className='coll'>
                    <select className='input' name="likelihood" onChange={onChangeLikelihood} defaultValue={likelihood}>
                        {selectOption}
                    </select>
                </div>
                
                <div className='coll'>
                    <select className='input' name="criticality" onChange={onChangeCriticality} defaultValue={criticality}>
                        {selectOption}
                    </select>
                </div>
                <div className='coll'>
                    <Button onClick={()=>setChangeMod(false)} variant="outlined" startIcon={<SaveIcon />} >Зберегти</Button>
                </div>
            </div>)

    } else {
        result = <div className="row">
            <div className='coll'>
                <div className="params" > {description}  </div>
            </div>
        
            <div className='coll'>
                <div className="params" > {RiskLevel[likelihood].getDescription()}  </div>
            </div>
            
            <div className='coll'>
                <div className="params" > {RiskLevel[criticality].getDescription()}  </div>
            </div>
            <div className='coll'>
                <Button onClick={save} variant="outlined" startIcon={<EditIcon />} >Редагувати</Button>
                <Button onClick={remove} variant="outlined" color="warning"  startIcon={<DeleteIcon />} >Видалити</Button>
            </div>
        </div>
    }

    return result;
}