import { IListRiskItems } from "../Interfaces/IRiskItem";
import { CreateRiskItem } from "./CreateRiskItem";
import { RiskItem } from "./RiskItem";


export const RiskList:React.FC<IListRiskItems> = (props) =>{
    const list = props.list
    .map(item => 
        <RiskItem key={item.id} change={props.change} remove={props.remove} {...item} />
    );
    return <> 
        <CreateRiskItem add={props.add} />
        <h2>Список ризиків</h2>
        { list }
        </>;
}